@import "../../main.scss";


.wrapper {
    position: absolute;
    width: 100%;
}

.section {
    position: relative;
    height: 100vh;
    overflow: hidden;
}

.treeBackground {
    position: absolute;
    height: 140vh;
    top: -30vh;
    right: -20vh;
    z-index: -10;

}

.container {
    margin-bottom: 0;
    padding: rem(80px) rem(80px);

    @include desktop1440 {
        padding: rem(80px) rem(40px);
    }
}

.description {
    margin-top: rem(50px);
    max-width: rem(550px);
    color: $thirdColor;
}

.title {
    font-size: rem(48px);
    margin-bottom: rem(30px);
}

.descriptionText {
    font-size: rem(20px);
    font-weight: 400;
}