@import "../../main.scss";


.wrapper {
    position: relative;
    height: rem(40px);
    width: rem(40px);
    background-color: $fifthColor;
    border-radius: rem(20px);
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: background-color .5s;
    margin-right: rem(10px);

    &:last-child {
        margin-right: 0;
    }

    &:hover {
        cursor: pointer;
        background-color: $fourthColor;

    }
}

.user {
    position: relative;
    // left: 12.5%;
    // right: 12.31%;
    top: 10%;
    // bottom: 6.2%;
    fill: black;
    stroke: black;
}